import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';
import NumberInput from '../library/numberinput.js';


class EditCrashpadDetail extends Component {

    render() {
        var Components = [TextInput, FileInput, Select,Select, Select, Select, Select, TextArea, NumberInput];

        var title = {'name':'title', 'value':'','label':'Title (What people searching see first.)'}
        var main_image = {'name':'main_image','label':'Main Photo of Your Crashpad', 'multiple':false}
        var privacy = {'value':'','name':'privacy','label':'Room Privacy', 'defaultoption': 'Choose A Room Privacy Option', 'options':[{'value':'Private','text':'Private Room(s)'},{'value':'Shared','text':'Shared Room(s)'},{'value':'Shared/Private','text':'Both Shared and Private Rooms'}]}
        var style = {'value':'','name':'style','label':'Bed Style', 'defaultoption': 'Choose A Bed Style', 'options':[{'value':'Hot','text':'Hot Beds'},{'value':'Cold','text':'Cold Beds'},{'value':'Hot/Cold','text':'Both Hot and Cold Beds'},{'value':'Other','text':'Other'}]}
        var bedrooms = {'value':'','name':'bedrooms','label':'Number of Bedrooms', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5+','text':'5+'}]}
        var beds = {'value':'','name':'beds','label':'Number of Beds', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5','text':'5'},{'value':'6','text':'6'},{'value':'7','text':'7'},{'value':'8','text':'8'},{'value':'9','text':'9'},{'value':'10+','text':'10+'}]}
        var baths = {'value':'','name':'bathrooms','label':'Number of Bathrooms', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5+','text':'5+'}]}
        var description = {'label':'Write A Description of Your Crashpad','name':'description','value':''};
        var price = {'label':'How much does your crashpad cost? (Per Month)','name':'price','value':''};

        var ComponentProps = [title, main_image, privacy, style, bedrooms, beds, baths, description, price];
        var defaults = {'main_image':'', 'privacy':'', 'style':'', 'bedrooms':1, 'bathrooms':1,'beds':1,'description':'','user':this.props.user_id, 'price':0.00};

        var submitUrl = "/api/home/crashpad/" + this.props.crashpad_id + '/';

        var content = <div className="container">
                <h3>Details About Your Crashpad</h3>
                <Form components={Components} first={true} componentProps={ComponentProps} dataUrl={submitUrl} objectName={'crashpad'} submitUrl={submitUrl} redirectUrl={'/editCrashpadPhotos/' + this.props.crashpad_id + '/'} defaults={defaults} />
                </div>

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default EditCrashpadDetail;



