import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';

import List from '../library/list.js';

import Checklist from '../library/checklist.js';

class EditCrashpadAmenities extends Component {

    render() {

        var checkbox_amenities = {'name':'amenities[]','label':'{name}', 'value':'{id}'}
        var amenities_list = {'dataUrl':'/api/home/amenities/', 'objectName':'amenities','dataMapping':checkbox_amenities, 'name':'amenities[]'}
        var dataUrl = '/api/home/amenities/?values_list=id&crashpads=' + this.props.crashpad_id;
        var dataMapping = {'amenities[]':'{amenities}'};

        var content = <div className="container">
            <h3>What Amenities do you have?</h3>
            <Form components={[Checklist]} componentProps={[amenities_list]} dataUrl={dataUrl} dataMapping={dataMapping} defaults={{'amenities[]':''}} submitUrl={'/api/home/crashpad/' + this.props.crashpad_id + '/'}  redirectUrl={'/editCrashpadPreferences/' + this.props.crashpad_id + '/'} />
            </div>;

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default EditCrashpadAmenities;


