import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';
import Checkbox from '../library/checkbox.js';
import List from '../library/list.js';
import Button from '../library/button.js';

import Checklist from '../library/checklist.js';

class EditCrashpadPhotos extends Component {
    constructor(props) {
      super(props);
      this.skipPhotos = this.skipPhotos.bind(this);
    }

    skipPhotos() {
      window.location.href = '/editCrashpadAmenities/' + this.props.crashpad_id + '/';
    }

    render() {
        var Components = [FileInput];
        var photos = {'name':'photos[]', 'multiple':true, 'deleteUrl':'/api/home/photos/{id}/delete/'}

        var ComponentProps = [photos];
        var defaults = {'photos[]':[],'crashpad':this.props.crashpad_id, 'multiple':'photos[]'};

        var submitUrl = "/api/home/photos/";
        var dataMapping = {'photos[]':'{photos}'};
        var dataUrl = '/api/home/photos/?order_by=order&crashpad=' + this.props.crashpad_id;
        var content = <div className="container">
            <h3>Add Additional Photos of Your Crashpad</h3>
            <Form components={Components} dataUrl={dataUrl} dataMapping={dataMapping} objectName={'photos'} componentProps={ComponentProps} submitUrl={submitUrl} defaults={defaults} redirectUrl={'/editCrashpadAmenities/' + this.props.crashpad_id + '/'} />
            <Button type={'danger'} text={'Skip Adding Photos'} clickHandler={this.skipPhotos} />
            </div>;

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default EditCrashpadPhotos;



