import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import StripeMain from '../library/stripemain.js';


class StripePayment extends Component {
    constructor(props) {
        super(props);
        this.state = {'user_name':'', 'plan_id':'', 'plan_name':''};

    }

    choosePlan(plan_id, plan_name) {
        this.setState({'plan_id':plan_id,'plan_name':plan_name})
    }

    render() {

         var stripe = <div></div>;
        stripe = <div>
                    <h3>Easily Pay With A Credit Card</h3>
                    <p>Monthly Plan</p>
                    <StripeMain {...this.props} stripe_public_key={'pk_live_ta0sBG8BX5jCgzrpOORDXmjv'} plan_id={'plan_DdVT011c1BSaDG'} />
                </div>

        return (
            <div className="container">
            <div class="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
              <h1 class="display-4">Start Showing Your Crashpad</h1>
              <p class="lead">You are moments away from advertising your crashpad on crashpad connect!</p>
            </div>
            <div class="card-deck mb-3 text-center">
                <div class="card mb-4 box-shadow">
                  <div class="card-header">
                    <h4 class="my-0 font-weight-normal">Let's Get Started</h4>
                  </div>
                  <div class="card-body">
                    <h1 class="card-title pricing-card-title">$24.99 <small class="text-muted"> one time set-up fee</small></h1>
                    <h1 class="card-title pricing-card-title">$7.99 <small class="text-muted">/ mo</small></h1>
                    <ul class="list-unstyled mt-3 mb-4">
                      <li>List Your Crashpad on the website</li>
                      <li>Pay Monthly</li>
                    </ul>
                    </div>
                </div>
              </div>


            {stripe}

            </div>

             );
    }
}
export default StripePayment;



