import React, { Component } from 'react';
import resolveVariables from '../base/resolver.js';
import ajaxWrapper from '../base/ajax.js';

class OwnerCrashCard extends React.Component {
    constructor(props) {
        super(props);

        this.redirect = this.redirect.bind(this);
        this.unpublish = this.unpublish.bind(this);
        this.unpublishCallback = this.unpublishCallback.bind(this);
        this.unpublishCallback2 = this.unpublishCallback2.bind(this);

    }

    unpublish() {
        var data = {'crashpad_id':this.props.id}
        ajaxWrapper("POST",'/stripeCancel/', data, this.unpublishCallback);
    }

    delete() {
      ajaxWrapper("POST",'/api/home/crashpad/' + this.props.id + '/delete/', {}, this.unpublishCallback2);
    }

    unpublishCallback() {
        var data = {'published':false};
        ajaxWrapper("POST",'/api/home/crashpad/' + this.props.id + '/', data, this.unpublishCallback2);
    }

    unpublishCallback2() {
        this.props.refreshData();
    }

    redirect(url, blank) {
        if (blank == true) {
            window.open(url, '_blank');
        }
        else {
            window.location.href = url;
        }

    }

    render() {
        var image = <div></div>;
        if (this.props.imageUrl) {
            image = <img className="card-img-top" src={this.props.imageUrl} alt={this.props.imageAlt} />
        }

        var extraClass = '';
        if (this.props.cssClass) {
            extraClass = ' ' + this.props.cssClass;
        }

        var buttons = [];
        if (this.props.id != '0') {
            buttons.push(<button className="btn btn-primary" onClick={() => this.redirect("/editCrashpadAddress/" + this.props.id + "/", false)} target="_blank">Edit</button>)
            buttons.push(<button className="btn btn-info" onClick={() => this.redirect(this.props.link, true)} target="_blank">Preview</button>)
            if (this.props.published == "true") {
                buttons.push(<button className="btn btn-danger" onClick={this.unpublish}>Un-Publish</button>);

            }
            else {
                buttons.push(<button className="btn btn-success" onClick={() => this.redirect("/publish/" + this.props.id + "/", false)}>Publish</button>);
                buttons.push(<button className="btn btn-danger" onClick={() => this.delete(this.props.id)}>Delete</button>);
            }
        }



        return (
            <div className="col-md-4 col-xs-12"><a className="card" data-id={this.props.data_id} href={this.props.link}>
              {image}
              <div className="card-body">
                <div>{this.props.privacy} Rooms | {this.props.beds} {this.props.style} Beds</div>
                <h5 className="card-title" style={{fontWeight:'bold'}}>{this.props.title}</h5>
                <div>${this.props.price} per month</div>
              </div>
          </a>

           {buttons}
          </div>
        );
    }
}

export default OwnerCrashCard;



