import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';

class OwnerSignUp extends Component {

    render() {
        var Components = [TextInput,TextInput,TextInput,TextInput, PasswordInput, FileInput, HiddenInput];
        var first_name_props = {'value':'','name':'first_name','label':'First Name:','placeholder': 'First Name'}
        var last_name_props = {'value':'','name':'last_name','label':'Last Name:','placeholder': 'Last Name'}
        var email_props = {'value':'','name':'email','label':'Email:','placeholder': 'crash@pad.com'}
        var phone_props = {'value':'','name':'phone','label':'Phone Number:','placeholder': '(555) 555-5555'}
        var image_props = {'name':'imageUrl','label':'Your Picture'}
        var password_props = {'confirm_password':true};
        var hidden_props = {'name':'type', 'value':'Owner'}

        var ComponentProps = [first_name_props, last_name_props, email_props, phone_props, password_props, image_props, hidden_props];
        var defaults = {'first_name':'','last_name':'', 'email':'', 'phone':'', 'imageUrl':'', 'password':'','type':'Owner'};

        var submitUrl = "/api/user/user/";

        var content = <div className="container"><h2>Sign Up and List Your Crashpad in Minutes.</h2><Form components={Components} redirectUrl={'/login/'} componentProps={ComponentProps} submitUrl={submitUrl} defaults={defaults} /></div>

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default OwnerSignUp;


