import React, { Component } from 'react';
import './App.css';

import ajaxWrapper from "./base/ajax.js";

import ClientApp from "./clientApp.js";

import Header from './base/header.js';

import Wrapper from './base/wrapper.js';
import Home from './pages/home.js';

import Index from './pages/index.js';

import MyCrashPads from './pages/mycrashpads.js';
import CrashpadSearch from './pages/crashpadSearch.js';
import ViewCrashpad from './pages/viewCrashpad.js';

import EditCrashpadPreferences from './pages/newCrashpadPreferences.js';
import EditCrashpadAmenities from './pages/newCrashpadAmenities.js';
import EditCrashpadAddress from './pages/newCrashpadAddress.js';
import EditCrashpadDetail from './pages/newCrashpadDetail.js';
import EditCrashpadPhotos from './pages/newCrashpadPhotos.js';
import StripePayment from './pages/stripePayment.js';
import PaymentConfirmed from './pages/paymentConfirmed.js';

import OwnerSignUp from './pages/ownersignup.js';
import LogIn from './pages/logIn.js';
import SignUp from './pages/signup.js';

import AppList from './pages/appList.js';
import ModelList from './pages/modelsList.js';
import InstanceList from './pages/modelInstances.js';
import Instance from './pages/instance.js';

import UserNav from './localLibrary/userNav.js';
import OwnerNav from './localLibrary/ownerNav.js';

import Footer from './localLibrary/footer.js';

import FileInput from './library/fileinput.js';

import PasswordResetRequest from './pages/passwordResetRequest.js';
import PasswordReset from './pages/passwordReset.js';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: true,
            csrfmiddlewaretoken: undefined
        };

        this.ajaxCallback = this.ajaxCallback.bind(this);
    }

    componentDidMount() {
        ajaxWrapper("GET", "/csrfmiddlewaretoken/", {}, this.ajaxCallback);
        var user_name = localStorage.getItem('user_name')
        var user_id = localStorage.getItem('user_id')
        console.log("Href",window.location.href);
        if (user_name && user_id) {
            this.setState({user_id:user_id,user_name:user_name}, this.redirect())

        }
    }

    redirect() {
        if (window.location.href.indexOf('logIn') > -1) {
            window.location.href = '/myCrashpads/';
        }
    }

    ajaxCallback(value){
        console.log(value);
        window.secretReactVars["csrfmiddlewaretoken"] = value.csrfmiddlewaretoken;
        this.setState({loaded: true, csrfmiddlewaretoken: value.csrfmiddlewaretoken});

    }

    getURL(){
        var url = window.location.pathname;
        if (url[0] == '/'){ url = url.substring(1);}
        if (url[url.length - 1] == '/'){ url = url.substring(0,url.length-1);}

        return url.split('/');
    }

    logOut() {
        localStorage.removeItem('user_id');
        localStorage.removeItem('user_name');

        window.location.href = '/';
    }

    render() {
        console.log(this.props);
        var params = this.getURL();
        console.log("Params", params);

        var loading = <h1>Loading . . . </h1>;
        var content = null;
        var navbar = <UserNav user_id={this.state.user_id} />
        if (params[0] === ""){
            //Home page
            content = <Index user_id={this.state.user_id} />

        } else if (params[0] === "ownerSignUp") {
            //List components
            content = <OwnerSignUp />;
        }
        else if (params[0] === "signUp") {
            //List components
            content = <SignUp />;
        }
        else if (params[0] === "editUser") {
            //List components
            content = <SignUp user_id={this.state.user_id} />;
        }
        else if (params[0] === "login") {
            //List components
            content = <LogIn />;
        }
        else if (params[0].toLowerCase() === "mycrashpads") {
            //List components
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <MyCrashPads user_id={this.state.user_id} />;
        }
        else if (params[0] === "editCrashpadAddress") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <EditCrashpadAddress crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0] === "editCrashpadDetail") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <EditCrashpadDetail crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0] === "editCrashpadPhotos") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <EditCrashpadPhotos crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0] === "editCrashpadAmenities") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <EditCrashpadAmenities crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0] === "editCrashpadPreferences") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <EditCrashpadPreferences crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0] === "publish") {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <StripePayment crashpad_id={params[1]} user_id={this.state.user_id} />;
        }
        else if (params[0] == "appList") {
            content = <AppList />;
        }
        else if (params[0] == "models") {
            content = <ModelList app={params[1]} />
        }
        else if (params[0] == "modelInstances") {
            content = <InstanceList app={params[1]} model={params[2]} />
        }
        else if (params[0] == "instance") {
            content = <Instance app={params[1].toLowerCase()} model={params[2].toLowerCase()} id={params[3]} />
        }
        else if (params[0] == "crashpadSearch") {
            content = <CrashpadSearch user_id={this.state.user_id} airport_code={params[1]} />
        }
        else if (params[0] == 'viewCrashpad') {
            if (this.state.user_id) {
              content = <ViewCrashpad crashpad_id={params[1]} user_id={this.state.user_id} />
            }
            else {
              content = content = <SignUp crashpadRedirect={params[1]} />;
            }

        }
        else if (params[0] == 'previewCrashpad') {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <ViewCrashpad crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0].toLowerCase() == 'paymentconfirmed') {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <PaymentConfirmed crashpad_id={params[1]} user_id={this.state.user_id} />
        }
        else if (params[0].toLowerCase() == 'passwordresetrequest') {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <PasswordResetRequest user_id={this.state.user_id} />
        }
        else if (params[0].toLowerCase() == 'passwordreset') {
            navbar = <OwnerNav user_id={this.state.user_id} />
            content = <PasswordReset user_id={params[1]} />
        }
        else if (params[0] == 'logOut') {
            this.logOut();
        }


        return (
            <div className="App">
                <OwnerNav user_id={this.state.user_id} />
                {(this.state.loaded) ? content : loading}
                <Footer />
            </div>
        );
    }
}

export default App;



