import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import StripeMain from '../library/stripemain.js';


class PaymentConfirmed extends Component {

    render() {

        return (
            <div className="container">
              <div class="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
                <h1 class="display-4">Your crashpad is live.</h1>
                <p class="lead">Your payment was successful. You can edit any details at <a href='/myCrashpads/' style={{'color':'#007bff'}}>My Crashpads</a></p>
              </div>
            </div>
             );
    }
}
export default PaymentConfirmed;



