import React, { Component } from 'react';
import resolveVariables from '../base/resolver.js';

class CrashCard extends React.Component {

    render() {
        var image = <div></div>;
        if (this.props.imageUrl) {
            image = <img className="card-img-top" src={this.props.imageUrl} alt={this.props.imageAlt} />
        }

        var extraClass = '';
        if (this.props.cssClass) {
            extraClass = ' ' + this.props.cssClass;
        }

        return (
            <div className="col-md-4 col-sm-6 col-xs-12" style={{'marginBottom':'10px'}}><a className="card" data-id={this.props.data_id} href={this.props.link}>
              {image}
              <div className="card-body">
                <div>{this.props.privacy} Rooms | {this.props.beds} {this.props.style} Beds</div>
                <h5 className="card-title" style={{fontWeight:'bold'}}>{this.props.title}</h5>
                <div>${this.props.price} per month</div>
              </div>
          </a></div>
        );
    }
}


export default CrashCard;



