import React, { Component } from 'react';
import Wrapper from '../base/wrapper.js';
import Select from '../library/select.js';
import Form from '../library/form.js';

import Responsive from 'react-responsive';

const Desktop = props => <Responsive {...props} minWidth={992} />;
const Tablet = props => <Responsive {...props} minWidth={768} maxWidth={991} />;
const Mobile = props => <Responsive {...props} maxWidth={767} />;
const Default = props => <Responsive {...props} minWidth={768} />;


class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: true,
            height: '600'
        };
    }

    componentDidMount() {
        this.setState({height: window.innerHeight});
    }

    setGlobalState(name,state) {
        var airport_code = state['airport_code'];
        window.location.href = '/crashpadSearch/' + airport_code + '/';
    }


    render() {

    var airports = [{'value':'ATL','text':'ATL - Atlanta'},
                    {'value':'BOS','text':'BOS - Boston'},
                    {'value':'BWI','text':'BWI - Baltimore/Washington'},
                    {'value':'CLT','text':'CLT - Charlotte'},
                    {'value':'DCA','text':'DCA - Washington D.C.'},
                    {'value':'DEN','text':'DEN - Denver'},
                    {'value':'DFW','text':'DFW - Dallas/Ft. Worth'},
                    {'value':'DTW','text':'DTW - Detroit'},
                    {'value':'EWR','text':'EWR - Newark'},
                    {'value':'FLL','text':'FLL - Ft. Lauderdale'},
                    {'value':'HNL','text':'HNL - Honolulu'},
                    {'value':'IAD','text':'IAD - Dulles'},
                    {'value':'IAH','text':'IAH - Houston'},
                    {'value':'JFK','text':'JFK - New York (JFK)'},
                    {'value':'LAS','text':'LAS - Las Vegas'},
                    {'value':'LAX','text':'LAX - Los Angeles'},
                    {'value':'LGA','text':'LGA - New York (LaGuardia)'},
                    {'value':'MIA','text':'MIA - Miami'},
                    {'value':'MCO','text':'MCO - Orlando'},
                    {'value':'MDW','text':'MDW - Chicago (Midway)'},
                    {'value':'MSP','text':'MSP - Minneapolis/St. Paul'},
                    {'value':'ORD','text':"ORD - Chicago (O'Hare)"},
                    {'value':'PDX','text':'PDX - Portland'},
                    {'value':'PHL','text':'PHL - Philadelphia'},
                    {'value':'PHX','text':'PHX - Phoenix'},
                    {'value':'SAN','text':'SAN - San Diego'},
                    {'value':'SEA','text':'SEA - Seattle'},
                    {'value':'SFO','text':'SFO - San Francisco'},
                    {'value':'SLC','text':'SLC - Salt Lake City'},
                    {'value':'TPA','text':'TPA - Tampa'}];
        var background = {
            backgroundImage: 'url(https://wallscover.com/images/flight-5.jpg)',
            minHeight: this.state.height + 'px',
            backgroundPosition: 'bottom',
            backgroundSize: 'cover'
        }
        var whiteText = {
            color: 'white',
            textShadow: '1px 1px 10px rgba(0,0,0, .5)',
            fontWeight: 'bold'
        }


        var Components = [Select];
        var airport_code = {'value':'','name':'airport_code',
            'defaultoption': 'Select Base', 'options':airports, 'layout': 'col-12'}

        var ComponentProps = [airport_code];
        var defaults = {'airport_code':''};

        var submitUrl = "/api/home/crashpad/";


        var form = <Form components={Components} autoSetGlobalState={true}
            setGlobalState={this.setGlobalState} globalStateName={'form'}
            componentProps={ComponentProps} redirectUrl={'/crashpadSearch/{id}/'}
            defaults={defaults} classCss={''} />;

        var listCrashpadLink = '/signUp/';
        if (this.props.user_id) {
          listCrashpadLink = '/myCrashPads/';
        }

        var content =
        <div style={background}>
        <div className="container" style={{textAlign:'center'}}>
            <Default>
            <br/><br/><br/><br/><br/><br/><br/>
            </Default>
            <Mobile>
            <br/>
            </Mobile>
            <div style={{width: '80%', margin:'0px auto', textAlign:'left'}}>
            <h1 style={whiteText} >SEARCH</h1>
            <h3 style={whiteText} >Select your airport base below for a list of crashpads</h3>
            {form}
            <a href={listCrashpadLink} style={whiteText}>List Your Crashpad</a>
            </div>
        </div></div>;


        return (
            <Wrapper loaded={this.state.loaded} content={content} />
        );
    }
}

export default Index;



