import React, { Component } from 'react';
import resolveVariables from '../base/resolver.js';
import NavBar from '../library/navbar.js';

class UserNav extends React.Component {

    render() {
        var links;

        var listCrashpadLink = '/signUp/';
        if (this.props.user_id) {
          listCrashpadLink = '/myCrashPads/';
        }

        links = <div><a class="dropdown-item" href="/signUp/">Sign Up</a>
        <a class="dropdown-item" href={listCrashpadLink}>My Crashpad</a>
        <a class="dropdown-item" href="/login/">Log In</a></div>

        if (this.props.user_id) {
          links = <div><a class="dropdown-item" href="/editUser/">My Account</a>
          <a class="dropdown-item" href={listCrashpadLink}>My Crashpad</a>
          <a class="dropdown-item" href="/logOut/">Log Out</a></div>
        }

        return (
          <div className="container-fluid">
          <nav class="navbar navbar-expand-lg navbar-light bg-light">
                  <a class="navbar-brand" href="/"><img src='/static/images/logo.png?v=2' style={{'height':'40px'}}/></a>
                  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                      <span class="navbar-toggler-icon"></span>
                  </button>
                  <div id="navbarNavDropdown" class="navbar-collapse collapse">
                      <ul class="navbar-nav mr-auto">
                      </ul>

                      <ul class="navbar-nav" style={{'padding-right':'30px'}}>
                        <li class="nav-item">
                            <a class="nav-link" href={listCrashpadLink}>List Your Crashpad</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="/crashpadSearch/">Search</a>
                        </li>
                            <li class="nav-item dropdown">
                                <a class="nav-link dropdown-toggle" href='#' id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                  Account
                                </a>
                                <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                    {links}
                                </div>
                            </li>
                        </ul>
                  </div>
              </nav>
              </div>
        );
    }
}



export default UserNav;



