import React, { Component } from 'react';

import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import { Carousel } from 'react-responsive-carousel';
import Image from '../library/image.js';
import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import TextArea from '../library/textarea.js';
import Button from '../library/button.js';
import Responsive from 'react-responsive';

const Desktop = props => <Responsive {...props} minWidth={992} />;
const Tablet = props => <Responsive {...props} minWidth={768} maxWidth={991} />;
const Mobile = props => <Responsive {...props} maxWidth={767} />;
const Default = props => <Responsive {...props} minWidth={768} />;

class ViewCrashpad extends Component {
    constructor(props) {
        super(props);
        this.state = {
            crashpad: {},
            loaded:false,
            subject:'',
            message:'',
            date: '',
            mobileImageView: 'none',
            message_sent: false,
        };

        this.ajaxCallback = this.ajaxCallback.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.sendMessageCallback = this.sendMessageCallback.bind(this);
        this.toggleMobileImages = this.toggleMobileImages.bind(this);
    }

    componentDidMount() {
        var dataUrl = "/api/home/crashpad/" + this.props.crashpad_id + "/?related=user,photos,amenities,preferences";
        ajaxWrapper("GET", dataUrl, {}, this.ajaxCallback);
    }

    ajaxCallback(value) {
        console.log(value);
        this.setState({'crashpad':value[0].crashpad, loaded:true});
    }

    rememberPage(url) {
        localStorage.setItem('redirect', window.location.href);
        window.location.href = url;
    }

    compare(a,b) {
        if (a.order < b.order) {
            return -1;
        } else {
            return 1;
        }
    }

    handleChange = (e) => {
      console.log('Handle Change');
       var name = e.target.getAttribute("name");
       var newState = {};
       newState[name] = e.target.value;

       console.log(newState)
        var newCompleteState = this.state;
        newCompleteState[name] = e.target.value;
       this.setState(newState);
    }

    sendMessage() {
        ajaxWrapper('GET','/api/user/user/' + this.props.user_id + '/',{},this.sendMessageCallback)
    }

    sendMessageCallback(result) {
      ajaxWrapper('POST','/api/email/',{'to_email': this.state.crashpad.user.email, 'subject':this.state.subject,'message':this.state.message + '<br /><br /> Requested Start Date: ' + this.state.date + '<br /><br /> sent from: ' + result[0]['user']['first_name'] + ' ' + result[0]['user']['last_name'] + ' <br /> Email: ' + result[0]['user']['email'], 'from_email':'support@crashpadconnect.com'})
      this.setState({'message_sent':true})
      console.log("message",this.state.subject, this.state.message)
    }

    toggleMobileImages() {
      if (this.state.mobileImageView == 'none') {
        this.setState({mobileImageView: 'block'})
      }
      else {
        this.setState({mobileImageView: 'none'})
      }
    }

    render() {
        var data = this.state.crashpad;
        var images = [];
        var mainImage;
        var mobileImages = [];
        var amenities = [];
        var preferences = [];
        var contactCard = null;

        var photos = [];
        photos.push({'photos':{'url':data.main_image}});

        if (data.photos) {
            data.photos.sort(this.compare);
            for (var index in data.photos) {
                photos.push(data.photos[index])
            }
        }
        console.log("Photos",photos)
        console.log(data.photos)

        if (this.state.loaded) {
            for (var i=0; i< photos.length; i++){

                images.push(
                    <Image src={photos[i].photos.url} css={{'height':'400px','width':'auto'}} />
                );
                if (i == 0) {
                  mainImage = <Image src={photos[i].photos.url} css={{'width':'100%', 'marginBottom':'10px'}} />;
                }
                else {
                mobileImages.push(
                  <Image src={photos[i].photos.url} css={{'width':'100%',  'marginBottom':'10px'}} />
                );
              }
            }
            //<a className='btn btn-info' href={"mailto:" + data.user.email}>{data.user.email}</a>
            var messageSent = <div></div>
            if (this.state.message_sent == true) {
              messageSent = <div><p className="alert alert-success">Message Sent</p></div>;
            }

            if (this.props.user_id) {
            contactCard =
            <div className='card' style={{padding: '20px'}}>
                <div>
                    <p>Interested in this crashpad? Contact {data.user.first_name} to request availability</p>
                    <TextInput name={'subject'} value={this.state.subject} handlechange={this.handleChange} label={'Subject'} placeholder={"I'm looking to rent a bed in September"}/>
                    <TextInput name={'date'} value={this.state.date} handlechange={this.handleChange} label={'Requested Start Date'} placeholder={"10/2/2018"}/>
                    <TextArea name={'message'} value={this.state.message} handlechange={this.handleChange} label={'Message'} placeholder={"I'm looking to rent a bed in September..."} />
                    <Button clickHandler={this.sendMessage} text={'Send'} type={'success'} />
                    {messageSent}
                </div>
            </div>;
        }


            for (var i=0; i<data.amenities.length; i++){
                amenities.push(
                    <li>{data.amenities[i].amenities.name}</li>
                );
            }
            for (var i=0; i<data.preferences.length; i++){
                preferences.push(
                    <li>{data.preferences[i].preferences.name}</li>
                );
            }
        }



        var pictureMessage = 'Show All Images';
        if (this.state.mobileImageView =='block') {
          pictureMessage = 'Hide Images'
        }

        var imageDisplay = {};
        if (photos[0].photos.url == '/static/images/crashpadHome.png' && photos.length == 1) {
          console.log("HERE")
          imageDisplay['display'] = 'none';
        }
        return (
        <div>
            <div style={imageDisplay}>
                <Default>
                <Carousel emulateTouch={true} infiniteLoop={true} showThumbs={false}>
                    {images}
                </Carousel>
                </Default>
                <Mobile>
                {mainImage}

                <div style={{display: this.state.mobileImageView}}>
                {mobileImages}
                </div>
                <Button type={'success'} text={pictureMessage} clickHandler={this.toggleMobileImages} />
                </Mobile>
            </div>

            <div className='container'>
                <div className='row'>
                    <div className='col-12'><br/></div>
                    <div className='col-md-8 col-xs-12'>
                        <div>${data.price} per month</div>
                        <h1 className="card-title" style={{fontWeight:'bold'}}>{data.title}</h1>
                        <div>
                            {data.bedrooms} {data.privacy} Bedrooms | {data.beds} {data.style} Beds | {data.bathrooms} Bathrooms
                            <br/><br/>
                            <p>{data.description}</p>
                        </div>

                        <div className='card' style={{padding: '20px'}}>
                            <h6 style={{fontWeight: 'bold'}}>Amenities</h6>
                            <ul>
                                {amenities}
                            </ul>
                        </div>
                        <br/>

                        <div className='card' style={{padding: '20px'}}>
                            <h6 style={{fontWeight: 'bold'}}>Preferences</h6>
                            <ul>
                                {preferences}
                            </ul>
                        </div>

                        <br/><br/><br/>
                    </div>
                    <div className='col-md-4 col-xs-12'>
                        {contactCard}
                    </div>
                </div>
            </div>
        </div>
        );
    }
}

export default ViewCrashpad;



