import React, { Component } from 'react';
import resolveVariables from '../base/resolver.js';
import NavBar from '../library/navbar.js';

class UserNav extends React.Component {

    render() {
        var links;
        if (this.props.user_id) {
            links = [['/','Crashpad Connect'],['/crashpadSearch/','Search Crashpads'],['/myCrashpads/','List My Crashpad'],['/logOut/','Log Out']]
        }
        else {
            links = [['/','Crashpad Connect'],['/crashpadSearch/','Search Crashpads'],['/ownerSignUp/','List My Crashpad'],['/login/','Log In']]
        }

        var name = <img src='/static/images/logo.png' style={{'height':'40px'}}/>

        //var name = 'Crashpad';

        return (
            <NavBar links={links} name={name} nameLink={"/"} />
        );
    }
}



export default UserNav;



