import React, { Component } from 'react';

import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import Card from '../library/card.js';
import List from '../library/list.js';

import OwnerCrashCard from '../localLibrary/ownerCrashCard.js';


class MyCrashPads extends Component {

    render() {

        //list
        var dataMapping = {
            'button_type':'primary',
            'button':'View',
            'id':'{id}',
            'link': '/viewCrashpad/{id}/',
            'street':'{street}',
            'city':'{city}',
            'imageUrl':'{main_image}',
            'amenities':'{amenities}',
            'preferences':'{preferences}',
            'description':'{description}',
            'price': '{price}',
            'privacy': '{privacy}',
            'beds': '{beds}',
            'title':'{title}',
            'style':'{style}',
            'published':'{published}'
        };


        var lastInstanceData = {
            'button_type':'primary',
            'button':'View',
            'id':'0',
            'link': '/editCrashpadAddress/',
            'street':'None',
            'city':'None',
            'imageUrl':'/static/images/crashpadHome.png',
            'description':'{description}',
            'price': '100s',
            'privacy': '',
            'beds': '0',
            'title':'Start Advertising Your Next Crashpad in 5 minutes.',
            'style':'',
            'published': '{published}',
            'stripe_subscription_id': '{stripe_subscription_id}',
        };

        var crashPads =
        <List className="row" noDataMessage={""}
            dataUrl={"/api/home/crashpad/?user="+this.props.user_id}
            component={OwnerCrashCard}
            objectName={'crashpad'} dataMapping={dataMapping}
             lastInstanceData={lastInstanceData} />;

        var content =
        <div className='container'>
			<h2>My Crashpads</h2>

			{crashPads}
		</div>

        return (
            <Wrapper loaded={true} content={content} />
         );
    }
}
export default MyCrashPads;



