import React, { Component } from 'react';

import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import Card from '../library/card.js';
import List from '../library/list.js';

import CrashCard from '../localLibrary/crashCard.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';
import Header from '../library/header.js';
import NumberInput from '../library/numberinput.js';

import Checklist from '../library/checklist.js';

import Responsive from 'react-responsive';

const Desktop = props => <Responsive {...props} minWidth={992} />;
const Tablet = props => <Responsive {...props} minWidth={768} maxWidth={991} />;
const Mobile = props => <Responsive {...props} maxWidth={767} />;
const Default = props => <Responsive {...props} minWidth={768} />;


class CrashpadSearch extends Component {
    constructor(props) {
        super(props);
        var airport_code = this.props.airport_code;
        if (airport_code == undefined) {
            airport_code = '';
        }
        this.state = {'filters':{'airport_code':airport_code, 'privacy':'', 'style':'', 'bedrooms__gte':'1', 'bathrooms__gte':'1','beds__gte':'1', 'preferences__in':[],'amenities__in':[],'published':true}};

        this.setGlobalState = this.setGlobalState.bind(this);
    }

    setGlobalState(name, value) {
        var newGlobalState = this.state;

        for (var index in value) {
            newGlobalState[name][index] = value[index];
        }

        this.setState(newGlobalState)
    }

    render() {
        var airports = [{'value':'ATL','text':'ATL - Atlanta'},
                    {'value':'BOS','text':'BOS - Boston'},
                    {'value':'BWI','text':'BWI - Baltimore/Washington'},
                    {'value':'CLT','text':'CLT - Charlotte'},
                    {'value':'DCA','text':'DCA - Washington D.C.'},
                    {'value':'DEN','text':'DEN - Denver'},
                    {'value':'DFW','text':'DFW - Dallas/Ft. Worth'},
                    {'value':'DTW','text':'DTW - Detroit'},
                    {'value':'EWR','text':'EWR - Newark'},
                    {'value':'FLL','text':'FLL - Ft. Lauderdale'},
                    {'value':'HNL','text':'HNL - Honolulu'},
                    {'value':'IAD','text':'IAD - Dulles'},
                    {'value':'IAH','text':'IAH - Houston'},
                    {'value':'JFK','text':'JFK - New York (JFK)'},
                    {'value':'LAS','text':'LAS - Las Vegas'},
                    {'value':'LAX','text':'LAX - Los Angeles'},
                    {'value':'LGA','text':'LGA - New York (LaGuardia)'},
                    {'value':'MIA','text':'MIA - Miami'},
                    {'value':'MCO','text':'MCO - Orlando'},
                    {'value':'MDW','text':'MDW - Chicago (Midway)'},
                    {'value':'MSP','text':'MSP - Minneapolis/St. Paul'},
                    {'value':'ORD','text':"ORD - Chicago (O'Hare)"},
                    {'value':'PDX','text':'PDX - Portland'},
                    {'value':'PHL','text':'PHL - Philadelphia'},
                    {'value':'PHX','text':'PHX - Phoenix'},
                    {'value':'SAN','text':'SAN - San Diego'},
                    {'value':'SEA','text':'SEA - Seattle'},
                    {'value':'SFO','text':'SFO - San Francisco'},
                    {'value':'SLC','text':'SLC - Salt Lake City'},
                    {'value':'TPA','text':'TPA - Tampa'}];


        var Components = [Header,Select,NumberInput, NumberInput, Select, Select, Select, Select, Select];

        var header = {'layout': 'col-12', 'size':2,'text':'Filters'}
        var airport_code = {'layout': 'col-md-4', 'value':this.props.airport_code,'name':'airport_code', 'label':'Airport Code', 'defaultoption': 'Select Base', 'options':airports}
        var lowPrice = {'layout': 'col-md-4', 'label':'Minimum Price','name':'price__gte','value':''};
        var highPrice = {'layout': 'col-md-4', 'label':'Maximum Price','name':'price__lte','value':10000};
        var privacy = {'layout': 'col-md-4', 'value':'','name':'privacy','label':'Room Privacy', 'defaultoption': 'Choose A Room Privacy Option', 'options':[{'value':'Private','text':'Private Room(s)'},{'value':'Shared','text':'Shared Room(s)'},{'value':'Both','text':'Both Shared and Private Rooms'}]}
        var style = {'layout': 'col-md-4', 'value':'','name':'style','label':'Bed Style', 'defaultoption': 'Choose A Bed Style', 'options':[{'value':'Hot','text':'Hot Beds'},{'value':'Cold','text':'Cold Beds'},{'value':'Both','text':'Both Hot and Cold Beds'},{'value':'Other','text':'Other'}]}
        var bedrooms = {'layout': 'col-md-4', 'value':'','name':'bedrooms__gte','label':'Number of Bedrooms', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5','text':'5+'}]}
        var beds = {'layout': 'col-md-4', 'value':'','name':'beds__gte','label':'Number of Beds', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5','text':'5'},{'value':'6','text':'6'},{'value':'7','text':'7'},{'value':'8','text':'8'},{'value':'9','text':'9'},{'value':'10','text':'10+'}]}
        var baths = {'layout': 'col-md-4', 'value':'','name':'bathrooms__gte','label':'Number of Bathrooms', 'defaultoption': '1', 'options':[{'value':'1','text':'1'},{'value':'2','text':'2'},{'value':'3','text':'3'},{'value':'4','text':'4'},{'value':'5','text':'5'}]}

        var ComponentProps = [header, airport_code, lowPrice, highPrice, privacy, style, bedrooms, beds, baths];

        var submitUrl = "/api/home/crashpad/" + this.props.crashpad_id + '/';


        var checkbox_amenities = {
            'name':'amenities__in',
            'label':'{name}',
            'value':'{id}'
        }
        var amenities_list = {
            'dataUrl':'/api/home/amenities/',
            'objectName':'amenities',
            'dataMapping':checkbox_amenities,
            'title':'Amenities',
            'name':'amenities__has'
        }

        var amenities =
            <Form components={[Checklist]} componentProps={[amenities_list]}
                defaults={{'amenities':this.state.filters.amenities}}
                setGlobalState={this.setGlobalState} globalStateName={'filters'}
                autoSetGlobalState={true} layout="col-6" />;

        var checkbox_preferences = {
            'name':'preferences__in',
            'label':'{name}',
            'value':'{id}'
        }
        var preferences_list = {
            'dataUrl':'/api/home/preferences/',
            'objectName':'preferences',
            'dataMapping':checkbox_preferences,
            'title':'Member Preferences',
            'name':'preferences__has'
        }

        var preferences =
            <Form components={[Checklist]} componentProps={[preferences_list]}
                defaults={{'preferences':this.state.filters.preferences}}
                setGlobalState={this.setGlobalState} globalStateName={'filters'}
                autoSetGlobalState={true} layout="col-6" />;

        //list
        var dataMapping = {
            'button_type':'primary',
            'button':'View',
            'id':'{id}',
            'link': '/viewCrashpad/{id}/',
            'street':'{street}',
            'city':'{city}',
            'imageUrl':'{main_image}',
            'amenities':'{amenities}',
            'preferences':'{preferences}',
            'description':'{description}',
            'price': '{price}',
            'privacy': '{privacy}',
            'beds': '{beds}',
            'title':'{title}',
            'style':'{style}'
        };
        var crashPads =
        <List className="row" noDataMessage={"No Crashpads Available with these filters. Please try adjusting."}
            dataUrl={"/api/home/crashpad/?related=amenities,preferences"}
            filters={this.state.filters} component={CrashCard}
            objectName={'crashpad'} dataMapping={dataMapping} />;

        var content =
        <div className='container'>
			<h2>Results Near Your Base</h2>
			<p>Find the perfect crashpad based on your search requirements below.</p>

            <div id="accordion">
                <div id="headingOne">
                    <button class="btn btn-info" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                        Filter
                    </button>
                </div>

                <div id="collapseOne" class="collapse" aria-labelledby="headingOne" data-parent="#accordion">
                    <div class="card-body">
                    <Default>
                      <Form layout={"row"} components={Components} componentProps={ComponentProps}
                        defaults={this.state.filters} setGlobalState={this.setGlobalState}
                        globalStateName={'filters'} autoSetGlobalState={true} />
                        <div className='row'>
                			{amenities}
                			{preferences}
                        </div>
                      </Default>
                      <Mobile>
                      <Form components={Components} componentProps={ComponentProps}
                        defaults={this.state.filters} setGlobalState={this.setGlobalState}
                        globalStateName={'filters'} autoSetGlobalState={true} />
                			{amenities}
                			{preferences}
                      </Mobile>

                    </div>
                </div>
            </div>
            <br/>

			{crashPads}
		</div>

        return (
            <Wrapper loaded={true} content={content} />
         );
    }
}
export default CrashpadSearch;



