import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';
import getComponent from '../componentResolver.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';
import Checkbox from '../library/checkbox.js';
import List from '../library/list.js';

import Checklist from '../library/checklist.js';

class EditCrashpadPreferences extends Component {

    render() {
        var checkbox_preferences = {'name':'preferences','label':'{name}', 'value':'{id}'}
        var preferences_list = {'component':Checkbox, 'dataUrl':'/api/home/preferences/', 'objectName':'preferences', 'dataMapping':checkbox_preferences, 'name':'preferences[]'}
        var dataUrl = '/api/home/preferences/?values_list=id&crashpads=' + this.props.crashpad_id;
        var dataMapping = {'preferences[]':'{preferences}'};

        var content = <div className="container">
            <h3>Do you have any preferences for your guests?</h3>
            <Form components={[Checklist]} componentProps={[preferences_list]} dataUrl={dataUrl} dataMapping={dataMapping} defaults={{'preferences[]':''}} submitUrl={'/api/home/crashpad/'+ this.props.crashpad_id + '/'} redirectUrl={'/myCrashPads/' + this.props.crashpad_id + "/"} />
            </div>;

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default EditCrashpadPreferences;


