import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import FileInput from '../library/fileinput.js';
import HiddenInput from '../library/hiddeninput.js';
import PasswordInput from '../library/passwordinput.js';
import Select from '../library/select.js';
import GoogleAddress from '../library/googleaddress.js';
import TextArea from '../library/textarea.js';
import NumberInput from '../library/numberinput.js';

class EditCrashpadAddress extends Component {

    render() {
        var airports = [{'value':'ATL','text':'ATL - Atlanta'},
                    {'value':'BOS','text':'BOS - Boston'},
                    {'value':'BWI','text':'BWI - Baltimore/Washington'},
                    {'value':'CLT','text':'CLT - Charlotte'},
                    {'value':'DCA','text':'DCA - Washington D.C.'},
                    {'value':'DEN','text':'DEN - Denver'},
                    {'value':'DFW','text':'DFW - Dallas/Ft. Worth'},
                    {'value':'DTW','text':'DTW - Detroit'},
                    {'value':'EWR','text':'EWR - Newark'},
                    {'value':'FLL','text':'FLL - Ft. Lauderdale'},
                    {'value':'HNL','text':'HNL - Honolulu'},
                    {'value':'IAD','text':'IAD - Dulles'},
                    {'value':'IAH','text':'IAH - Houston'},
                    {'value':'JFK','text':'JFK - New York (JFK)'},
                    {'value':'LAS','text':'LAS - Las Vegas'},
                    {'value':'LAX','text':'LAX - Los Angeles'},
                    {'value':'LGA','text':'LGA - New York (LaGuardia)'},
                    {'value':'MIA','text':'MIA - Miami'},
                    {'value':'MCO','text':'MCO - Orlando'},
                    {'value':'MDW','text':'MDW - Chicago (Midway)'},
                    {'value':'MSP','text':'MSP - Minneapolis/St. Paul'},
                    {'value':'ORD','text':"ORD - Chicago (O'Hare)"},
                    {'value':'PDX','text':'PDX - Portland'},
                    {'value':'PHL','text':'PHL - Philadelphia'},
                    {'value':'PHX','text':'PHX - Phoenix'},
                    {'value':'SAN','text':'SAN - San Diego'},
                    {'value':'SEA','text':'SEA - Seattle'},
                    {'value':'SFO','text':'SFO - San Francisco'},
                    {'value':'SLC','text':'SLC - Salt Lake City'},
                    {'value':'TPA','text':'TPA - Tampa'}];

        var Components = [Select, NumberInput, GoogleAddress];
        var airport_code = {'value':'','name':'airport_code','label':'Airport Code', 'defaultoption': 'Type To Select A Code', 'options':airports}
        var driveDistance = {'value':'', 'name':'driveDistance','label':'Distance from Airport (miles)'}
        var google_address = {'names':['address','street','street2','city','state','zip']}

        var ComponentProps = [airport_code, driveDistance, google_address];
        var defaults = {'airport_code':'','google_address':'', 'user':this.props.user_id};

        var submitUrl = "/api/home/crashpad/";

        var content;
        if (this.props.crashpad_id) {
            submitUrl = "/api/home/crashpad/" + this.props.crashpad_id + "/";
            var dataUrl = "/api/home/crashpad/" + this.props.crashpad_id + "/";
            content = <div className="container">
                <h3>Address Info</h3>
                <Form components={Components} first={true} dataUrl={dataUrl} objectName={'crashpad'} componentProps={ComponentProps} submitUrl={submitUrl} redirectUrl={'/editCrashpadDetail/' + this.props.crashpad_id + '/'} defaults={defaults} />
                </div>;
        }
        else {
            content = <div className="container">
                <h3>Address Info</h3>
                <Form components={Components} componentProps={ComponentProps} submitUrl={submitUrl} redirectUrl={'/editCrashpadDetail/{id}/'} objectName={'crashpad'} defaults={defaults} />
                </div>;
        }

        return (
            <Wrapper loaded={true} content={content} />
             );
    }
}
export default EditCrashpadAddress;


