import React, { Component } from 'react';
import ajaxWrapper from "../base/ajax.js";
import Wrapper from '../base/wrapper.js';

import Form from '../library/form.js';
import TextInput from '../library/textinput.js';
import Select from '../library/select.js';
import PasswordInput from '../library/passwordinput.js';
import Alert from '../library/alert.js';
import Header from '../library/header.js';
import Button from '../library/button.js';


class PasswordResetRequest extends Component {
    constructor(props) {
        super(props);
        this.state = {'email':'', 'error':false, 'sent':false, 'loaded':true}
        this.emailCallback = this.emailCallback.bind(this);
        this.email = this.email.bind(this);
        this.userLookup = this.userLookup.bind(this);
    }

    handleChange = (e) => {
       var name = e.target.getAttribute("name");
       var newState = {};
       newState[name] = e.target.value;

        var newCompleteState = this.state;
        newCompleteState[name] = e.target.value;
       this.setState(newState);
    }

    userLookup() {
      ajaxWrapper('GET','/api/user/user/?email=' + this.state.email, {}, this.email)
    }

    email(result) {
      console.log("Email",result)
      if (result.length > 0) {
        var user = result[0]['user']
        ajaxWrapper('POST','/api/email/', {'to_email':user.email, 'from_email':'support@crashpadconnect.com','subject':'Password Reset','message':'You can reset your password <a clicktracking=off href="https://www.crashpadconnect.com/passwordReset/' + user.id +'/">by following this link.</a>'}, this.emailCallback)
      }
      else {
        this.setState({'error':true})
      }
    }

    emailCallback(result) {
      console.log("Email Sent");
      this.setState({sent:true})
    }

    render() {
        var email_props = {'value':this.state.email,'name':'email','label':'Email:','placeholder': 'support@crashpadconnect.com', 'handlechange':this.handleChange}

        var submitUrl = "/api/user/user/";

        var alert = <div></div>;
        if (this.state.error == true) {
          alert = <Alert text={'No user found with that email.'} type={'danger'} />
        }

        var sent = <div></div>;
        if (this.state.sent == true) {
          sent = <Alert text={'Your password reset email has been sent.'} type={'success'} />
        }

        var content = <div className="container">
                <Header size={2} text={"Add your email and we'll send you a link to reset your password."} />
                <TextInput {...email_props} />
                <Button type={'success'} text={'Reset Password'} clickHandler={this.userLookup} />
                {alert}
                {sent}
        </div>;

        return (
          <div>
            <Wrapper loaded={this.state.loaded}  content={content} />
          </div>
             );
    }
}
export default PasswordResetRequest;



